.registerPage {
  margin: 10px auto;
  padding-bottom: 10px;
  width: 320px;
  background-color: rgb(255, 255, 225);
  border: 1px solid rgb(190, 190, 190);
  border-radius: 6px;
  box-shadow: 4px 4px 10px rgb(141, 141, 141);
}

.title {
  margin-top: 10px;
  text-align: center;
  color: rgb(40, 40, 0);
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}

.form {
  display: flex;
  flex-direction: column;
  margin: 10px auto 0;
  width: 300px;

  background-color: rgb(255, 255, 170);
  border: 1px solid rgb(190, 190, 190);
  border-radius: 6px;
}

.label {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  padding: 0 5px;

  font-size: 18px;
  font-weight: 500;
}

.input {
  margin-top: 6px;
  padding: 0 5px;
  height: 25px;

  border: 1px solid rgb(190, 190, 190);
  border-radius: 4px;

  font-size: 14px;
  line-height: 1;
}

.submitButton {
  cursor: pointer;
  margin: 10px auto;
  height: 30px;

  background-color: rgb(255, 255, 50);
  border: 1px solid rgb(190, 190, 190);
  border-radius: 6px;

  font-size: 16px;
  transition: text-shadow 250ms, color 250ms,
    background-color 250ms;
}

.submitButton:hover,
.submitButton:focus {
  color: rgb(255, 255, 255);
  background-color: rgb(255, 230, 0);
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}
